<template>
  <div class="home">
    <transition
      name="loading"
      enter-active-class="animated fadeOut"
      leave-active-class="animated fadeOut"
    >
      <div class="loading" v-if="showLoading">
        <img src="../assets/loading.svg" alt />
      </div>
    </transition>
    <HomeList></HomeList>
    <!--  -->
    <div class="personalized-box">
      <CardTitle></CardTitle>

      <ul class="songlist">
        <SongCardList
          :songlist="personalized"
          v-for="(personalized,index) in personalizeds"
          :key="index"
          :currentSong="courrntSong"
        ></SongCardList>
      </ul>
    </div>

    <!--  -->
    <div class="newsong">
      <CardTitle></CardTitle>

      <ul>
        <SongList
          :type="type"
          :songs="newsong"
          :currentSong="courrntSong"
          v-for="(newsong,index) in newsongs"
          :key="index"
          @transferId="$emit('transferId',$event)"
        ></SongList>
      </ul>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import CardTitle from "@/components/CardTitle.vue";
import SongCardList from "@/components/SongCardList.vue";
import SongList from "@/components/SongList.vue";
import HomeList from "@/components/HomeList.vue";
export default {
  name: "Home",
  props: {
    courrntSong: Object
  },
  data: function() {
    return {
      personalizeds: [],
      newsongs: [],
      type: "new",
      showLoading: true
    };
  },
  components: {
    CardTitle,
    SongCardList,
    SongList,
    HomeList
  },
  methods: {
    // ajax
    getPersonalizedFn: function() {
      var _this = this;
      this.axios
        .get("/personalized", {
          params: {
            limit: 6
          }
        })
        .then(function(response) {
          // console.log(response.data.result);
          _this.personalizeds = response.data.result;
          window.localStorage.setItem(
            "personalizeds",
            JSON.stringify({
              expies: Date.now() + 10 * 60 * 1000,
              result: response.data.result
            })
          );
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // ajax
    getNewsongFn: function() {
      var _this = this;
      this.axios
        .get("/personalized/newsong")
        .then(function(response) {
          // console.log(response.data.result);
          _this.newsongs = response.data.result;
          window.localStorage.setItem(
            "newsongs",
            JSON.stringify({
              expies: Date.now() + 10 * 60 * 1000,
              result: response.data.result
            })
          );
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  created() {
    var personalizedsStorage = JSON.parse(
      window.localStorage.getItem("personalizeds")
    );
    if (personalizedsStorage && personalizedsStorage.expies > Date.now()) {
      //   
      this.personalizeds = personalizedsStorage.result;
    } else {
      //    ajax
      this.getPersonalizedFn();
    }

    var newsongs = JSON.parse(window.localStorage.getItem("newsongs"));
    if (newsongs && newsongs.expies > Date.now()) {
      //   
      this.newsongs = newsongs.result;
    } else {
      //    ajax
      this.getNewsongFn();
    }
  },
  // 
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.showLoading = false;
    });
  },
  beforeRouteLeave(to, from, next) {
    // 
    //  `this`
    // console.log(to, from, "home");
    this.showLoading = true;
    next();
  }
};
</script>
<style lang="less" scoped>
.home {
  width: 100%;
  height: 100%;
  .loading {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100vh;
    z-index: 99;
    background: rgba(255, 255, 255, 0.9);
    display: flex;
    img {
      width: 20%;
      height: auto;
      margin: auto;
    }
  }
}
.songlist {
  display: flex;
  flex-wrap: wrap;
}
</style>
